'use strict';

define('app/store',[
    'backbone'
], function(Backbone){

    var store;

    function Store(){
        this.store__ = {};
        this.supportStorage = false;
        this.events();
        if(Storage && sessionStorage){
            this.supportStorage = true;
        }
        this.initialize.apply(this, arguments);
    }

    _.extend(Store.prototype, Backbone.Events, {
        initialize: function(){
            
        },
        events: function(){

        },
        get: function(key, defaultValue){
            if(this.supportStorage){
                var data = sessionStorage.getItem(key);
                if(data === null){
                    return defaultValue;
                }
               
                try {
                    data = JSON.parse(data);
                } catch(ex) {
                    //
                }
                
                return data;
            }

            return this.store__[key] || defaultValue;
        },
        set: function(key, value){
            if(this.supportStorage){
                sessionStorage.setItem(key, value);
            } else {
                this.store__[key] = value;
            }
            
            return this;
        },
        remove: function(key){
            if(this.supportStorage){
                sessionStorage.removeItem(key);
            } else {
                this.store__[key] = null;
            }
        },
        clear: function(){
            if(this.supportStorage){
                sessionStorage.clear();
            } else {
                this.store__ = {};
            }
        }

    });

    store = new Store();
    store.Store = Store;

    return store;

});
