'use strict';

define('consumer/controllers/indexController',[
    'app/app',
    'consumer/consumer',
    'service/service',
    'app/base/controller',
    'consumer/views/indexView'
], function(
    app,
    consumer,
    service,
    BaseController,
    IndexView){

    var IndexController = BaseController.extend({
        events: function(){
            consumer.on('route:index', this.index, this);
            consumer.on('render:index', this.renderIndex, this);
        },
        routes: function(){
            return {
                '/consumer': 'index'
            }
        },
        index: function(){
            this.renderIndexView();
        },
        renderIndexView: function(options, callback){
            if(this.indexView){
                return;
            }
            this.indexView = new IndexView(options || {});
            this.listenTo(this.indexView, 'close', function(){
                this.indexView = null;
            });
            if(callback){
                callback(null, this.indexView);
            } else {
                app.renderView(this.indexView);
            }
        }
    });

    return IndexController;

});
