'use strict';

define('app/controllers/appController',[
    'app/app',
    'app/base/controller',
    'app/views/layoutView',
    'app/views/homeView',
    'service/service',
    'app/store',
    'app/views/error500'
], function(
    app,
    BaseController,
    LayoutView,
    HomeView,
    service,
    store,
    Error500) {

    var AppController;

    AppController = BaseController.extend({
        // Routes that need authentication and if user is not authenticated
        // gets redirect to login page
        requresAuth : [],

        // Routes that should not be accessible if user is authenticated
        // for example, login, register, forgetpasword ...
        preventAccessWhenAuth : ['#!/login'],

        before : function(params, next){
            //Checking if user is authenticated or not
            //then check the path if the path requires authentication 
            var isAuth = store.get('user.isLoggedIn', false);
            var path = Backbone.history.location.hash;
            var needAuth = _.contains(this.requresAuth, path);
            var cancleAccess = _.contains(this.preventAccessWhenAuth, path);

            if (needAuth && !isAuth) {
                //If user gets redirect to login because wanted to access
                // to a route that requires login, save the path in session
                // to redirect the user back to path after successful login
                store.set('redirectFrom', path);
                Backbone.history.navigate('!/login', { trigger : true, replace: true });
            } else if (isAuth && cancleAccess) {
                //User is authenticated and tries to go to login, register ...
                // so redirect the user to home page
                Backbone.history.navigate('!/', { trigger : true });
            }else{
                //No problem handle the route
                return next();
            }           
        },

        after : function(){
            //empty
        },
        execute: function() {
            return BaseController.__super__.execute.apply(this, arguments);
        },
        initialize: function() {
            AppController.__super__.initialize.apply(this, arguments);
        },
        events: function() {
            var self = this;
            app.on({
                'route:home': function() {
                    self.navigate('/', true);
                },
                'render:home': this.renderHome
            });
        },
        routes: {
            '/': 'home',
            '500': 'render500'
        },
        renderHome: function(options, callback){
            var homeView = new HomeView(options || {});
            this.listenTo(homeView, 'close', function(){
                homeView = null;
            });
            if(callback){
                callback(null, homeView);
            } else {
                homeView.render();
            }
        },
        home: function() {
            this.renderHome(null, function(err, view){
                app.renderView(view);
            });
        },
        render500: function(options, callback) {
            var view500 = new Error500();
            app.renderView(view500);
        }
    });

    return AppController;
});
