'use strict';

define('service/views/listView',[
    'service/service',
    'app/base/view',
    'service/views/listItemView',
    'text!service/templates/list.html'
], function(
    service,
    BaseView,
    ListItemView,
    listTemplate){

    var ListView = BaseView.extend({
        initialize: function(){
            ListView.__super__.initialize.apply(this, arguments);
            this.template = listTemplate;
        },
        render: function(context){
            ListView.__super__.render.apply(this, arguments);
            _.each(context && context.items.models || [], this.renderItem, this);
            return this;
        },
        renderItem: function(item){
            var listItemView = new ListItemView({parent: this}).render({model: item});
            this.$el.append(listItemView.el);
        }

    });

    return ListView;

});
