'use strict';

define('service/controllers/indexController',[
    'app/app',
    'service/service',
    'app/base/model',
    'app/base/controller',
    'service/views/listView',
    'service/views/detailView',
    'service/collections/serviceCollection',
    'app/store'
], function(
    app,
    service,
    Model,
    BaseController,
    ListView,
    DetailView,
    ServiceCollection,
    store){

    var listView, detailView, cached;
    var IndexController = BaseController.extend({
        // Routes that need authentication and if user is not authenticated
        // gets redirect to login page
        requresAuth : ['#!/service/\\d+'],

        // Routes that should not be accessible if user is authenticated
        // for example, login, register, forgetpasword ...
        preventAccessWhenAuth : ['#!/login'],

        before : function(params, next){
            //Checking if user is authenticated or not
            //then check the path if the path requires authentication 
            var isAuth = store.get('user.isLoggedIn', false);

            var path = Backbone.history.location.hash;
            var needAuth = _.findIndex(this.requresAuth, function(rpath) {
                return (new RegExp(rpath)).test(path);    
            });

            needAuth = (needAuth === -1) ? false : true;

            var cancleAccess = _.findIndex(this.preventAccessWhenAuth, function(rpath) {
                return (new RegExp(rpath)).test(path);    
            });

            cancleAccess = (cancleAccess === -1) ? false : true;

            if (needAuth && !isAuth) {
                // If user gets redirect to login because wanted to access
                // to a route that requires login, save the path in session
                // to redirect the user back to path after successful login
                store.set('redirectFrom', path);
                Backbone.history.navigate('!/login', { trigger : true, replace: true });
            } else if (isAuth && cancleAccess) {
                // User is authenticated and tries to go to login, register ...
                // so redirect the user to home page
                Backbone.history.navigate('!/', { trigger : true });
            } else {
                // No problem handle the route
                return next();
            }           
        },

        after : function(){
            //empty
        },
        events: function(){
            service.on('route:index', function(){
                this.navigate('/service', true);
            }, this);
            service.on('render:list', renderList, this);
            service.on('route:detail', function(id) {
                if ( ! store.get('user.isLoggedIn', false)) {
                } else {
                    this.navigate('/service/'+ id);
                }
            }, this);
            service.on('data:list', getServices, this);
            service.on('data:item', getServiceById, this);
        },
        routes: {
            '/service': 'index',
            '/service/:id': 'detail'
        },
        index: function(){
            renderList();
        },
        detail: function(id){
            renderDetail(id);
        }
    });

    return IndexController;

    function getServices() {
        return new ServiceCollection();
    }

    function getServiceById(id){
        return getServices().get(id);
    }

    function renderDetail(id, options, callback){
        if(detailView){
            return;
        }
        var model, data = getServiceById(id);
        if(!data){
            service.trigger('route:index', true);
            return;
        }
        model = new Model(data);
        data = {item: data};
        detailView = new DetailView(options || {});
        service.listenToOnce(detailView, 'close', function(){
            detailView = null;
        });
        if(callback){
            callback(null, detailView, data);
        } else {
            app.renderView(detailView, data);
        }
    }

    function renderList(options, callback){
        var self = this;
        
        var sCollection = getServices();
        sCollection.url = sCollection.url + 'GetAllServices';
        sCollection.fetch({
            success: function (collection, response) {
                var data = {items: collection};
                listView = new ListView(options || {});
                service.listenToOnce(listView, 'close', function () {
                    listView = null;
                });

                if(callback){
                    callback(null, listView, data);
                } else {
                    app.renderView(listView, data);
                }
            }
        })
        .fail(function(error) {
            self.fetchError(error);
        });
    }
});
