'use strict';

define('app/app',['backbone'], function(Backbone){

    var mapCompiled = {};
    var viewInstance;
    var baseUrl = 'http://serviceapp-1.apphb.com/Service.svc/';

    var app = {};

    _.extend(app, Backbone.Events, {
        renderView: renderView,
        renderTemplate: renderTemplate,
        apiConfig: {
            baseUrl: baseUrl,
            loginUrl: baseUrl + 'AuthenticateUser',
            registerUrl: baseUrl + 'InsertEngineer'
        }
    });

    app.on('view:remove', removeView);

    return app;

    function renderView(view){
        var args = _(arguments).slice(1).value();
        if(viewInstance){
            viewInstance.close();
        }
        viewInstance = view;
        render.call(this, view.render.apply(view, args).el);
    }

    function render(template){
        $('#shell').html(template);
        return this;
    }

    function renderTemplate(template, context){
        var compiled = _getCompiled(template);
        context = _.extend({}, context || {}, {
            route: Backbone.history.navigate
        });
        return compiled(context);
    }

    function _getCompiled(template){
        return mapCompiled[template] || (mapCompiled[template] = _.template(template || ''));
    }

    function removeView(view){
        view = null;
    }

});
