'use strict';

define('service/collections/serviceCollection',[
	'backbone', 
	'app/app', 
	'app/base/collection',
	'service/models/serviceModel'], function(Backbone, app, BaseCollection, ServiceModel){

    return BaseCollection.extend({
    	//model: ServiceModel,
    	url: app.apiConfig.baseUrl,
    	getAllServices: function () {
    		this.url = this.url + 'GetAllServices';
    		return this.fetch();
    	},
    	parse: function (response) {
    		var self = this;
    		_.each(response, function(val) {
    			self.push(new ServiceModel(val));
    		});

    		return this.models;
    	}
    });
});
