'use strict';

define('service/views/listItemView',[
    'service/service',
    'app/base/view',
    'text!service/templates/list-item.html'
], function(
    service,
    BaseView,
    listItemTemplate){

    var ListItemView = BaseView.extend({
        initialize: function(){
            ListItemView.__super__.initialize.apply(this, arguments);
            this.template = listItemTemplate;
        },
        className: 'col m4 s12',
        events: function(){
            return {
                'click .service-name': 'onClickServiceName'
            };
        },
        onClickServiceName: function(e){
            e = e || event;
            e.preventDefault();
            service.trigger('route:detail', true);
            return false;
        }
    });

    return ListItemView;

});
