'use strict';

define('auth/views/registerView',[
    'auth/auth',
    'app/base/view',
    'text!auth/templates/register.html'
], function(auth, BaseView, registerTemplate){

    var step = 1;

    var RegisterView = BaseView.extend({
        initialize: function(){
            RegisterView.__super__.initialize.apply(this, arguments);
            this.template = registerTemplate;
            this.data = {};
            this.isuser = 'true';
        },
        events: function(){
            return {
                'submit': 'onSubmit',
                'click #register_as_service_engineer': 'registerAsServiceEngineer',
                'click .btn-next': 'onClickNext',
                'click .btn-back': 'onClickBack',
                'keyup input': _.debounce(_.bind(this.onInputKeyup, this), 600)
            };
        },
        onInputKeyup: function(event){
            var data = {};
            var key = $(event.target).attr('name');
            var value = $(event.target).val();
            data[key] = value;
            this.isValid(data);
        },
        validation: function(){
            var self = this;
            return {
                email: [{
                    required: true,
                    msg: 'Please enter email address'
                }, {
                    pattern: 'email',
                    msg: 'Please enter valid email address'
                }],
                password: [{
                    required: true,
                    msg: 'Please enter password'
                }, {
                    minLength: 6,
                    msg: 'Password has to be min 6 characters long'
                }],
                password_confirm: [{
                    required: true,
                    msg: "Please re-enter password"
                }, {
                    fn: function(value){
                        var msg = 'Password confirmation does not match';
                        var password = self.$('[name="password"]').val();
                        if(value != password){
                            return msg;
                        }
                    }
                    
                }]
            }
        },
        render: function(context, template){
            var self = this;
            RegisterView.__super__.render.apply(self, arguments);
            self.$('select').material_select();
            self.$('.datepicker.dob').pickadate({
                selectMonths: true,
                selectYears: 100,
                max: new Date
            });
            self.$('.tabs').tabs();
            _.delay(function(){
                self.$('.tabs li > a.active').click();
            }, 300);
            return this;
        },
        onSubmit: function(event){
            event.preventDefault();
            var data = this.serialize();
            if(this.isValid(data)){
                data.isuser = this.isuser;
                auth.trigger('register', data, callback);
            }

            function callback(err, result){
                if(err){
                    auth.trigger('route:login', true);
                } else {
                    auth.trigger('route:login', true);
                }
            }

            return false;
        },
        registerAsServiceEngineer: function(event){
            this.$('#register_adhaar_or_pancard_field')[event.target.checked ? 'show' : 'hide']();
            this.isuser = event.target.checked ? 'false' : 'true';
        },
        onClickNext: function(event){
            event.preventDefault();
            var hash = this.serialize();
            if(step == 1){
                var data = _.pick(hash, 'email', 'password', 'password_confirm');
                if(this.isValid(data)){
                    step = 2;
                    this.$('[href="#register_info"]').click();
                }
            } else if(step == 2){
                var data = _.pick(hash, 'fname', 'lname', 'dob', 'gender', 'phone', 'cell');
                if(this.isValid(data)){
                    step = 3;
                    this.$('[href="#register_address"]').click();
                }
            }

            return false;
        },
        onClickBack: function(event){
            event.preventDefault();
            this.$('[href="#register_info"]').click();
            return false;
        }
    });

    return RegisterView;
});
