'use strict';

define('auth/controllers/authController',[
    'backbone',
    'app/app',
    'app/base/controller',
    'app/base/model',
    'app/store',
    'auth/auth',
    'auth/views/loginView',
    'auth/views/registerView'
], function(
    Backbone,
    app,
    BaseController,
    Model,
    store,
    auth,
    LoginView,
    RegisterView) {

    var loginView,
        loginModel,
        registerView,
        registerModel,
        bind = _.bind;

    var AuthController = BaseController.extend({
        // Routes that need authentication and if user is not authenticated
        // gets redirect to login page
        requresAuth : [],

        // Routes that should not be accessible if user is authenticated
        // for example, login, register, forgetpasword ...
        preventAccessWhenAuth : ['#!/login', '#!/register'],

        before: function (params, next) {
            //Checking if user is authenticated or not
            //then check the path if the path requires authentication 
            var isAuth = store.get('user.isLoggedIn', false);
            var path = Backbone.history.location.hash;
            var needAuth = _.contains(this.requresAuth, path);
            var cancleAccess = _.contains(this.preventAccessWhenAuth, path);

            if (needAuth && !isAuth) {
                //If user gets redirect to login because wanted to access
                // to a route that requires login, save the path in session
                // to redirect the user back to path after successful login
                store.set('redirectFrom', path);
                Backbone.history.navigate('!/login', { trigger : true, replace: true });
            } else if (isAuth && cancleAccess) {
                //User is authenticated and tries to go to login, register ...
                // so redirect the user to home page
                Backbone.history.navigate('!/', { trigger : true });
            } else {
                //No problem handle the route
                return next();
            }           
        },

        after : function(){
            //empty
        },
        initialize: initialize,
        execute: execute,
        events: events,
        routes: {
            '/login': 'login',
            '/register': 'register',
            '/logout': 'logout'
        },
        login: login,
        register: register,
        renderLogin: renderLogin,
        login_: login_,
        renderRegister: renderRegister,
        register_: register_,
        logout: logout
    });

    return AuthController;


    function initialize() {
        AuthController.__super__.initialize.apply(this, arguments);
    }

    function execute() {
        return AuthController.__super__.execute.apply(this, arguments);
    }

    function events() {
        var self = this;
        auth.on({
            'route:login': function() {
                self.navigate('login', true);
            },
            'route:register': function() {
                self.navigate('login', true);
            },
            'route:logout': function(){
                self.navigate('logout', true);
            },
            'render:login': bind(renderLogin, this),
            'render:register': bind(renderRegister, this),
            'login': bind(login_, this),
            'cancellogin': bind(cancelLogin, this),
            'register': bind(register_, this),
            'view:login:close': bind(closeLoginView, this)
        });
    }
    
    function login() {
        renderLogin.call(this, null, onRenderLogin);

        function onRenderLogin(err, view) {
            app.renderView(view);
        }
    }

    function register() {
        renderRegister.apply(this, [null, onRenderRegister]);

        function onRenderRegister(err, view) {
            app.renderView(view);
        }
    }

    function closeLoginView(){
        if(loginView){
            loginView.close();
            loginView = null;
            loginModel = null;
        }
    }

    function cancelLogin() {
        app.trigger('route:home');
    }

    function renderLogin(options, callback) {
        var self = this;
        if (loginView) {
            return;
        }
        loginView = new LoginView(options || {});
        this.listenTo(loginView, 'close', function(){
            loginView = null;
            loginModel = null;
        });
        if (callback) {
            callback(null, loginView);
        } else {
            loginView.render();
        }
    } 

    function login_(data, callback) {
        if (!loginModel) {
            loginModel = new Model();
            loginModel.urlRoot = app.apiConfig.loginUrl;
        } else {
            loginModel.clear();
        }

        loginModel.save(data, {success: success, error: error});

        function success(result) {
            store.set('logged_in', true);
            store.set('user.isLoggedIn', true);
            store.set('user', result);
            store.set('is_se', !data.isUser);
            if (store.get('redirectFrom', false)) {
                var redirect = store.get('redirectFrom');
                store.remove('redirectFrom');
                Backbone.history.navigate(redirect, {trigger: true});
            }
            app.trigger('route:home', true);
            auth.trigger('user:login', result);
        }

        function error(err) {
            callback(true);
        }
    }

    function renderRegister(options, callback) {
        var registerView = new RegisterView(options || {});
        if (callback) {
            callback(null, registerView);
        } else {
            registerView.render();
        }
    }

    function register_(data, callback) {
        if (!registerModel) {
            registerModel = new Model();
            registerModel.urlRoot = app.apiConfig.registerUrl;
        } else {
            registerModel.clear();
        }
        registerModel.save(data, {success: success, error: error});

        function success(result) {
            callback(null, result);
        }

        function error(err) {
            callback(true);
        }
    }

    function logout(){
        store.clear();
        _.delay(function(){
            app.trigger('route:home', true);
        }, 600);
    }

});
