'use strict';

define('service/views/detailView',[
    'app/base/view',
    'text!service/templates/detail.html'
], function(
    BaseView,
    detailTemplate){

    var DetailView = BaseView.extend({
        initialize: function(){
            DetailView.__super__.initialize.apply(this, arguments);
            this.template = detailTemplate;
        }
    });

    return DetailView;

});
