'use strict';

define('consumer/views/indexView',[
    'service/service',
    'app/base/view',
    'text!consumer/templates/index.html'
], function(
    service,
    BaseView,
    indexTemplate){

    var IndexView = BaseView.extend({
        initialize: function(){
            IndexView.__super__.initialize.apply(this, arguments);
            this.template = indexTemplate;
        },
        render: function(){
            IndexView.__super__.render.apply(this, arguments);
            this.renderServiceListView();
            return this;
        },
        renderServiceListView: function(){
            var self = this;
            service.trigger('render:list', {parent: this}, function(err, view){
                self.$(".service-list").html(view.render().el);
            });
        }
    });

    return IndexView;


});
