'use strict';

define('app/views/error500',[
    'app/base/view',
    'text!app/templates/500.html'
], function(
    BaseView,
    error500Template){

    var Error500 = BaseView.extend({
        initialize: function(){
            Error500.__super__.initialize.apply(this, arguments);
            this.template = error500Template;
        }
    });

    return Error500;

});
