'use strict';

define('app/views/homeView',[
    'app/app',
    'auth/auth',
    'app/store',
    'app/base/view',
    'text!app/templates/home.html',
    'service/service',
], function(
    app, 
    auth,
    store, 
    BaseView, 
    homeTemplate,
    service){

    var HomeView = BaseView.extend({
        initialize: function(){
            HomeView.__super__.initialize.apply(this, arguments);
            this.template = homeTemplate;
            auth.on({
                'user:login': _.bind(this.afterLogin, this),
                'user:register': _.bind(this.afterRegister, this)
            });
        },
        events: function(){
            return {
                'click.login-se .btn-login-se': 'onClickLoginSE',
                'click.login .btn-login': 'onClickLogin'
            }
        },
        render: function(){
            HomeView.__super__.render.apply(this, arguments);
            this.renderServiceView();
            return this;
        },
        renderServiceView: function () {
            var self = this;
            service.trigger('render:list', {}, function(err, view, obj) {
                self.$('#home').html(view.render.call(view, obj).el);
            });
        },
        renderRegisterView: function(){
            var self = this;
            auth.trigger('render:register', {parent: this}, function(err, view){
                self.$('#register').html(view.render().el);
            });
        },
        renderLoginView: function(){
            var self = this;
            auth.trigger('render:login', {parent: this}, function(err, view){
                self.$('#login').html(view.render().el);
            });
        },
        afterLogin: function(){
            this.$('#modal_login').closeModal();
            this.render();
        },
        afterRegister: function(){
            this.$(".btn-login.modal-trigger").click();
        },
        onClickLoginSE: function(event){
            event.preventDefault();
            this.$('#login_as_se')[0].checked = true;
            return false;
        },
        onClickLogin: function(event){
            event.preventDefault();
            this.$('#login_as_se')[0].checked = false;
            return false;
        }
    });

    return HomeView;

});
