'use strict';

define('app/base/view',[
    'backbone',
    'app/app',
    'auth/auth',
    'app/store'
], function(
    Backbone,
    app,
    auth,
    store) {

    var View;

    View = Backbone.View.extend({
        constructor: function() {
            View.__super__.constructor.apply(this, arguments);
        },
        initialize: function(options) {
            View.__super__.initialize.apply(this, arguments);
            if(options && options.parent){
                this.listenTo(options.parent, 'close', this.close);
            }
        },
        className: 'row',
        render: function(context, template) {
            this._render(context, template);

            return this;
        },
        serialize: function() {
            var $form;
            if (this.$el.is('form')) {
                $form = this.$el;
            } else {
                $form = this.$('form');
            }
            return $form.serializeObject();
        },
        _render: function(context, template) {
            var html = '',
                hasError = false;
            template = template || (context && context.template) || this.template || '';
            if (context && context.template) {
                delete context.template;
            }
            context = _.extend(context || {}, {
                isLoggedIn: store.get('logged_in', false),
                user: store.get('user', {}),
                isSE: store.get('is_se', false)
            });
            
            try {
                html = app.renderTemplate(template, context);
            } catch (ex) {
                hasError = true;
                console.trace('Template Parse Error', ex);
            }
            if (!hasError) {
                this.$el.html(html);
            }
            return this;
        },
        close: function() {
            View.__super__.remove.apply(this);
            this.trigger('close');
            app.trigger('view:remove', this);
        },
        remove: function() {
            View.__super__.remove.apply(this);
            this.trigger('close');
            app.trigger('view:remove', this);
        }
    });

    _.extend(View.prototype, Backbone.Validation.mixin);

    _.extend(View.prototype, {
        validation: {},
        isValid: function(data) {
            var validations = _.result(this, 'validation');
            _.each(validations, function(validation, attr) {
                this.valid(attr);
            }, this);
            var errors = this.preValidate(data);
            if (errors) {
                _.each(errors, function(error, attr) {
                    this.invalid(attr, error);
                }, this);
                return false;
            }
            return true;
        },
        invalid: function(attr, error, selector) {
            selector = selector || 'name';

            this.$('[' + selector + '~="' + attr + '"]')
                .addClass('invalid')
                .attr('data-error', error)
                .next('label')
                .attr('data-error', error);
        },
        valid: function(attr, selector) {
            selector = selector || 'name';
            this.$('[' + selector + '!="' + attr + '"]')
                .removeClass('invalid')
                .removeAttr('data-error')
                .next('label')
                .removeAttr('data-error');
        },
        attributes: function() {
            return [];
        }
    });

    return View;

});
