'use strict';

define('auth/views/loginView',[
    'backbone', 
    'app/app',
    'auth/auth',
    'app/store',
    'app/base/view',
    'text!auth/templates/login.html'
], function(
    Backbone,
    app,
    auth,
    store,
    BaseView,
    loginTemplate){

    var LoginView;

    LoginView = BaseView.extend({
        initialize: function(){
            LoginView.__super__.initialize.apply(this, arguments);
            this.template = loginTemplate;
        },
        events: function(){
            return {
                'submit': 'onSubmit',
                'click .modal-close': 'onCancelLogin'
            };
        },
        validation: function(){
            return {
                email: [{
                    required: true,
                    msg: 'Please enter email address'
                }, {
                    pattern: 'email',
                    msg: 'Please enter valid email address'
                }],
                password: [{
                    required: true,
                    msg: 'Please enter password'
                }]
            };
        },
        onCancelLogin: function (event) {
            event.preventDefault();
            auth.trigger('cancellogin');
        },
        onSubmit: function(event){
            event.preventDefault();
            var data = this.serialize();
            var self = this;
            if(this.isValid(data)){
                data.isUser = !self.$('#login_as_se')[0].checked+'';
                auth.trigger('login', data, callback);
            }
            
            function callback(err, result){
                if(err){
                    self.invalid('password', 'invalid email/password');
                }
            }
            return false;
        }
    });

    return LoginView; 
});
