'use strict';

define('app/bootstrap',[
    'backbone',
    'app/controllers/appController',
    'auth/controllers/authController',
    'service/controllers/indexController',
    'consumer/controllers/indexController'
], function(
    Backbone,
    AppController,
    AuthController,
    ServiceControler,
    ConsumerController){

    return {
        init: init
    };

    function init(){
        new AppController();
        new AuthController();
        new ServiceControler();
        new ConsumerController();
        Backbone.history.start();
        if(!!Backbone.history.location.hash){
            //
        } else {
            Backbone.history.navigate('!/', {trigger: true});
        }
        
    }

    (function() {
        var proxiedSync = Backbone.sync;

        $.ajaxSetup({
            statusCode: {
                401: function(){
                    Backbone.history.location.replace('#!/login');
                },
                403: function(){
                    Backbone.history.location.replace('#!/login');
                }
            }
        });

        Backbone.sync = function(method, model, options) {
            options || (options = {});
            if (!options.crossDomain) {
                options.crossDomain = true;
            }
            if (!options.xhrFields) {
                options.xhrFields = {withCredentials:true};
            }
            return proxiedSync(method, model, options);
      };
    })();
});
